import axios from 'axios';

const hostUrl = process.env.REACT_APP_HOST_URL;

const AttributesOverrideService = {
    getCategoryAndProfileLookup() {
        const url = hostUrl + 'AttributesOverride/CategoryAndProfileLookup';
        return axios.get(url);
    },

    getTitles(parameters) {
        const url = hostUrl + 'AttributesOverride/GetTitles' + parameters;
        return axios.get(url);
    },

    exporTitles(parameters) {
        const url = hostUrl + 'AttributesOverride/ExportTitles' + parameters;
        return axios.get(url, { responseType: 'arraybuffer' });
    },

    getTitle(titleId) {
        const url = hostUrl + 'AttributesOverride/GetTitleAttributes?titleId=' + titleId;
        return axios.get(url);
    },

    saveAttributesOverride(titleId, json) {
        const url = hostUrl + 'AttributesOverride/' + titleId;
        return axios.put(url, json);
    },

    getCertificateProfileLookup() {
        const url = hostUrl + 'AttributesOverride/GetCertificateProfileLookup';
        return axios.get(url);
    },

    getGenreProfileLookup() {
        const url = hostUrl + 'AttributesOverride/GetGenreProfileLookup';
        return axios.get(url);
    },

    getGradeProfileLookup() {
        const url = hostUrl + 'AttributesOverride/GetGradeProfileLookup';
        return axios.get(url);
    },
};

export default AttributesOverrideService;
