import React, { useEffect, useState, useRef } from 'react';

import styles from './AttributesOverride.module.css';
import {
  eyeIcon,
  eyeSlashIcon,
  attributesOverrideGreenIcon,
  attributesOverrideGreenToggledIcon,
} from '../../assets';

import Table from "../../shared/components/table/Table";
import BannerComponent from '../_reusable/banner/BannerComponent';
import attributesOverrideStorage from "./AttributesOverrideStorage";
import AttributesOverrideFormComponent from './attributes-override-form/AttributesOverrideFormComponent';
import AttributesOverrideService from './AttributesOverrideService';
import DateService from '../../core/date.service';
import AuthService from '../../core/auth.service';

const AttributesOverrideComponent = ({}) => {
  const searchRef = useRef();
  const { organisationName } = AuthService.getUserIdentity();
  const { tableHeadersLibrary, tableHeadersImport } = attributesOverrideStorage;
  const [renderTimes, setRenderTimes] = useState(0);
  const [isFetchingAttributes, setIsFetchingAttributes] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isExporting, setIsExporting] = useState(false);
  const [hasSearched, setHasSearched] = useState(false);
  const [titleSource, setTitleSource] = useState(true);
  const [searchKey, setSearchKey] = useState('');
  const [searchKeyHistory, setSearchKeyHistory] = useState('');
  const [queryParameters, setQueryParameters] = useState({ sortOrder: 1, sortBy: 1 });
  const [sortParameters, setSortParameters] = useState({ order: 1, sort: 1 });
  const [tableParameters, setTableParameters] = useState({});
  const [paginationParameters, setPaginationParameters] = useState({});
  const [data, setData] = useState([]);
  const [tableHeaders, setTableHeaders] = useState([]);
  const [selectedTitle, setSelectedTitle] = useState();
  const [isTableToggled, setIsTableToggled] = useState(false);

  const constructQueryString = (parameters) => {
    const pagination = `?page=${
      parameters.pageNumber ? parameters.pageNumber : 1
    }&size=${parameters.pageSize ? parameters.pageSize : 50}`;

    const titleName = parameters.titleNameFilter && parameters.titleNameFilterValue
      ? `&titleNameFilter=${parameters.titleNameFilter}&titleNameFilterValue=${parameters.titleNameFilterValue}`
      : '';

    const genre = parameters.genreFilter && parameters.genreFilterValue
      ? `&genreFilter=${parameters.genreFilter}&genreFilterValue=${parameters.genreFilterValue}`
      : '';

    const grade = parameters.gradeFilter && parameters.gradeFilterValue
      ? `&gradeFilter=${parameters.gradeFilter}&gradeFilterValue=${parameters.gradeFilterValue}`
      : '';

    const certificate = parameters.certificateFilter && parameters.certificateFilterValue
      ? `&certificateFilter=${parameters.certificateFilter}&certificateFilterValue=${parameters.certificateFilterValue}`
      : '';

    const year = parameters.yearFilter && parameters.yearFilterValue
      ? `&yearFilter=${parameters.yearFilter}&yearFilterValue=${parameters.yearFilterValue}`
      : '';
    
    const studio = parameters.studioFilter && parameters.studioFilterValue
      ? `&studioFilter=${parameters.studioFilter}&studioFilterValue=${parameters.studioFilterValue}`
      : '';

    const rating = parameters.ratingFilter && parameters.ratingFilterValue
      ? `&ratingFilter=${parameters.ratingFilter}&ratingFilterValue=${parameters.ratingFilterValue}`
      : '';

    const playRestrictions = parameters.playRestrictionsFilter && parameters.playRestrictionsFilterValue
      ? `&playRestrictionsFilter=${parameters.playRestrictionsFilter}&playRestrictionsFilterValue=${parameters.playRestrictionsFilterValue}`
      : '';

    const searchKey = parameters.searchKey
      ? `&searchKey=${parameters.searchKey}`
      : '';

    const sort = parameters.sortOrder && parameters.sortBy
      ? `&columnSort=${parameters.sortBy}&sortBy=${parameters.sortOrder}`
      : '';

    const isOverrode = parameters.isOverrode
      ? `&isOverrode=${parameters.isOverrode}`
      : '';

    let queryString = pagination
      + titleName
      + genre
      + grade
      + certificate
      + year
      + studio
      + rating
      + playRestrictions
      + searchKey
      + sort
      + isOverrode;

    if (queryString.includes('+')) {
      queryString = queryString.replace('+', '%2B');
    }

    return queryString;
  };

  const toggleTableData = () => {
    if (!isLoading) {
      setIsTableToggled(!isTableToggled);
      setQueryParameters({ ...queryParameters, isOverrode: !isTableToggled });
    }
  };

  const constructData = (data, titleSource) => {
    let list = [];

    if (titleSource) {
      list = data.map((item) => ({
        titleId: item.titleId,
        title: item.title,
        genre: item.overrodeValues.genre || item.genre,
        duration: item.overrodeValues.duration || item.duration,
        grade: item.overrodeValues.grade || item.grade,
        certificate: item.overrodeValues.certificate || item.certificate,
        year: item.overrodeValues.year || item.year,
        action: {
          id: item.titleId,
          icon: selectedTitle?.titleId === item.titleId ? eyeSlashIcon : eyeIcon,
          toolTip: 'View title details',
          actionName: 'viewTitleDetails',
        },
        isHighlighted: item.overrode,
      }));

      setTableHeaders(tableHeadersLibrary);
    } else {
      list = data.map((item) => ({
        titleId: item.titleId,
        title: item.title,
        studio: item.studio,
        rating: item.overrodeValues.rating || item.rating,
        grade: item.overrodeValues.grade || item.grade,
        playRestrictions: item.playRestrictions,
        action: {
          id: item.titleId,
          icon: selectedTitle?.titleId === item.titleId ? eyeSlashIcon : eyeIcon,
          toolTip: 'View title details',
          actionName: 'viewTitleDetails',
        },
        isHighlighted: item.overrode,
      }));

      setTableHeaders(tableHeadersImport);
    }

    return list;
  };

  const handleSetTableParameters = (parameters) => {
    setTableParameters({
      titleNameFilter: parameters.titleNameFilter,
      titleNameFilterValue: parameters.titleNameFilterValue,
      genreFilter: parameters.genreFilter,
      genreFilterValue: parameters.genreFilterValue,
      gradeFilter: parameters.gradeFilter,
      gradeFilterValue: parameters.gradeFilterValue,
      certificateFilter: parameters.certificateFilter,
      certificateFilterValue: parameters.certificateFilterValue,
      yearFilter: parameters.yearFilter,
      yearFilterValue: parameters.yearFilterValue,
      studioFilter: parameters.studioFilter,
      studioFilterValue: parameters.studioFilterValue,
      ratingFilter: parameters.ratingFilter,
      ratingFilterValue: parameters.ratingFilterValue,
      playRestrictionsFilter: parameters.playRestrictionsFilter,
      playRestrictionsFilterValue: parameters.playRestrictionsFilterValue,
    });
  };

  const onChangeSetSearchKey = (e) => {
    setSearchKey(e.target.value);
  };

  const handleGetTitles = () => {
    setIsLoading(true);

    AttributesOverrideService.getTitles(constructQueryString(queryParameters))
    .then((response) => {
      setData(constructData(response.data.assets, response.data.library));
      setTitleSource(response.data.library);
      setRenderTimes(renderTimes + 1);

      setPaginationParameters({
        totalCount: response.data.totalData,
        pageSize: response.data.size,
        currentPage: response.data.page,
        totalPages: response.data.totalPages,
      });
    })
    .finally(() => setIsLoading(false));
  };

  const handleOnSearch = () => {
    setQueryParameters({ ...queryParameters, searchKey, pageNumber: 1 })
    setSearchKeyHistory(searchKey);
    setHasSearched(true);
  };

  const onKeyDownTriggerSearch = (e) => {
    if (e.key === 'Enter') handleOnSearch();
  };

  const handleOnClearSearch = () => {
    setSearchKey('');
    setSearchKeyHistory('');
    setQueryParameters({ ...queryParameters, searchKey: '', pageNumber: 1 })
    setHasSearched(false);
  };

  const handleOnFilter = (data) => {
    setQueryParameters({ ...queryParameters, ...data, pageNumber: 1 })
  };

  const handleOnClearFilter = () => {
    setQueryParameters({
      searchKey,
      sortBy: queryParameters.sortBy,
      sortOrder: queryParameters.sortOrder,
    });
  };

  const handleOnSort = ({ sortBy, sortOrder }) => {
    setSortParameters({ order: sortOrder, sort: sortBy });
    setQueryParameters({ ...queryParameters, sortBy, sortOrder, pageNumber: 1 });
  };

  const handleOnPaginate = (pageNumber, pageSize  ) => {
    setQueryParameters({ ...queryParameters, pageNumber, pageSize });
  };

  const handleOnExport = () => {
    setIsExporting(true);

    AttributesOverrideService.exporTitles(constructQueryString(queryParameters))
      .then((response) => {
        const blob = new Blob([response.data], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        });
        saveAs(
          blob,
          `${organisationName}_op2mise_attributes_override_data_export_${DateService.toExportFormat(
            new Date(),
          )}.xlsx`,
        );
      })
      .finally(() => setIsExporting(false));

  };

  const constructItemsToDefault = (items) => {
    let data = items;
    data = data.map((item) => (
      { ...item, action: { ...item.action, icon: eyeIcon } }
    ));

    return data;
  };

  const constructItemsAfterViewing = (items, selectedId) => {
    let data = items;
    data = data.map((item) => (
      item.titleId === selectedId
        ? { ...item, action: { ...item.action, icon: eyeSlashIcon } }
        : { ...item, action: { ...item.action, icon: eyeIcon } }
    ));

    return data;
  };

  const handleGetTitle = (titleId) => {
    setIsFetchingAttributes(true);

    AttributesOverrideService.getTitle(titleId)
    .then((response) => {
      setSelectedTitle(response.data)
    })
    .finally(() => setIsFetchingAttributes(false));
  };

  const handleOnAction = (_, selectedId) => {
    const id = selectedTitle && selectedTitle.titleId === selectedId ? null : selectedId;
    if (id) {
      handleGetTitle(id);
      setData(constructItemsAfterViewing(data, id));
    } else {
      onHideFormPanel();
    }
  };

  const onHideFormPanel = () => {
    setSelectedTitle();
    setData(constructItemsToDefault(data));
  };

  useEffect(() => {
    handleSetTableParameters(queryParameters);
    handleGetTitles();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [queryParameters]);

  useEffect(() => {
    const backdropClicked = (e) => {
      if (searchRef.current && !searchRef.current.contains(e.target)) {
        setSearchKey(searchKeyHistory);
      }
    };

    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  });

    return (
      <>
        <BannerComponent componentName='Attributes Override' isSearchHidden />

        <hr style={{ borderTop: '1px  solid #b1b1b1', marginBottom: '8px' }} />
    
        <div className='d-flex'>
          <div>
            <div className='d-flex'>
              <div ref={searchRef} className={styles.searchWrapper}>
                <div className={styles.imageDiv}>
                  <img
                    className={styles.toggleAttributesTableIcon}
                    src={isTableToggled ? attributesOverrideGreenToggledIcon : attributesOverrideGreenIcon}
                    title={isTableToggled ? 'Show all titles' : 'Show titles with attributes override'}
                    alt=""
                    onClick={toggleTableData}
                  />
                </div>
              </div>
            </div>

          <Table
            noDataVerbiage="No programme titles found"
            tableHeight="281px"
            list={data}
            headers={tableHeaders}
            isExporting={isExporting}
            isFetchingData={isLoading}
            queryParameters={tableParameters}
            paginationParameters={paginationParameters}
            sortParameters={sortParameters}
            onFilter={handleOnFilter}
            onClearFilters={handleOnClearFilter}
            onSort={handleOnSort}
            onPaginate={handleOnPaginate}
            onExport={handleOnExport}
            onAction={handleOnAction}
          />
        </div>

          {(selectedTitle || isFetchingAttributes) && (
            <AttributesOverrideFormComponent
              isFetching={isFetchingAttributes}
              data={selectedTitle}
              getTitles={handleGetTitles}
              getTitleAttributes={() => handleGetTitle(selectedTitle.titleId)}
              onHide={onHideFormPanel}
              titleSource={titleSource}
            />
          )}
      </div>
    </>   
  );
};

export default AttributesOverrideComponent;
