import PropTypes from 'prop-types';
import Lottie from 'react-lottie';
import hourglassIcon from '../../../assets/lotties/3671-hour-glass-animation';

import styles from './styles.module.css';

function LottieGlassSpinner() {
    const defaultOptions = {
        loop: true,
        autoplay: true,
        animationData: hourglassIcon,
        rendererSettings: {
          preserveAspectRatio: 'xMidYMid slice',
        },
    };

    return (
        <div style={{ width: '100%', height: '100%', position: 'relative' }}>
            <div className={styles.lottieGlassSpinner}>
                <Lottie options={defaultOptions} height={120} width={120} />
            </div>
        </div>
    );
};

LottieGlassSpinner.propTypes = {
    size: PropTypes.string,
};
  
LottieGlassSpinner.defaultProps = {
    size: '40px',
};

export default LottieGlassSpinner;