import React, { useState, useEffect, useRef } from 'react';
import moment from 'moment';
import styles from './AttributesOverrideForm.module.css';
import { success } from '../../../shared/elements/notification/NotifyComponent';
import { closeIcon, saveIcon, closeCircleIcon, trashCanSmIcon, plusCircleIcon  } from '../../../assets';
import Input from '../../../shared/components/input/Input';
import AttributesOverrideStorage from '../AttributesOverrideStorage';
import AttributesOverrideService from '../AttributesOverrideService';
import LottieGlassSpinner from '../../../shared/components/lottie-glass-spinner/LottieGlassSpinner';
import TimeInput from '../../../shared/components/time-input/TimeInput';
import CustomDateRangeInput from '../../../shared/components/custom-date-range/CustomDateRangeInput';
import SelectComponent from '../../../shared/elements/select/select.component';
import { Certificate, Genre, Grade  } from '../../../constant/AttributeNameConstant';

const AttributesOverrideFormComponent = ({ isFetching, data, getTitles, getTitleAttributes, onHide, titleSource }) => {
    
    const { attributesOverrideFields } = AttributesOverrideStorage;
    const inputRef = useRef();
    const [inputRefName, setInputRefName] = useState('');
    const [isSaving, setIsSaving] = useState(false);
    const [parameters, setParameters] = useState({});
    const [startRangeIsRequiredMessage, setStartRangeIsRequiredMessage] = useState('');
    const [invalidDateRanges, setInvalidDateRanges] = useState([]);
    const [overlappingDates, setOverlappingDates] = useState([]);
    const [lessThanTodayDates, setLessThanTodayDates] = useState([]);
    const dateTodayWithoutTimeStamp = new Date(new Date().getFullYear(), new Date().getMonth(), new Date().getDate());
    const [maxId, setMaxId] = useState(0);
    const [certificateProfileLookup, setCertificateProfileLookup] = useState([]);
    const [genreProfileLookup, setGenreProfileLookup] = useState([]);
    const [gradeProfileLookup, setGradeProfileLookup] = useState([]);
    const [disabledLookups, setDisabledLookups] = useState({});
    const [isGettingCertificateLookup, setIsGettingCertificateLookup] = useState(true);
    const [isGettingGenreLookup, setIsGettingGenreLookup] = useState(true);
    const [isGettingGradeLookup, setIsGettingGradeLookup] = useState(true);

    const onChangeSetParameters = (e, label, type, isStartHistorical, isEndHistorical, id) => {      
        let value = e.target.value;
        if (type === 'number') {
            value = value.replace(/[^0-9]/g, "");
        }
        if(type === 'number' && value.length > 4) {
            return false
        }
        if(type === 'start_end_date'){
            const newParameters = {...parameters};
            let index = parseInt(label.split("-")[2]);
            let start = value.start ? moment(value.start).format("DD/MM/YYYY") : '';
            let end = value.end ? moment(value.end).format("DD/MM/YYYY") : '';
            newParameters.startEndDate[index].value = `${start},${end},${isStartHistorical || ''},${isEndHistorical || ''},${id || ''}`;
            newParameters.startEndDate[index].dayStart = e.target.value.dayStart;
            newParameters.startEndDate[index].dayEnd = e.target.value.dayEnd;
            newParameters.startEndDate[index].monthStart = e.target.value.monthStart;
            newParameters.startEndDate[index].monthEnd = e.target.value.monthEnd;
            newParameters.startEndDate[index].yearStart = e.target.value.yearStart;
            newParameters.startEndDate[index].yearEnd = e.target.value.yearEnd;
            setParameters(newParameters);
            return;
        }

        setParameters({ ...parameters, [e.target.name]: value });
    };
    
    const onChangeTimeInputSetParameters = (value, key) => {
        setParameters({ ...parameters, [key]: value });
    };

    const checkTitleSource = (source) => {
        const titleSourceText = titleSource ? 'Library' : 'Import'
        return source.includes(titleSourceText);
    };

    const hasParameters = () => {
        const result = Object.keys(parameters)
            .filter(key => key !== "startEndDate"
            && (parameters[key]
            && (parameters[key] !== '' || parameters[key] !== '00:00:00'))).length > 0;

        return result;
    }

    const checkDateRangeIfValid = () => {
        const invalidDates = [];
        parameters.startEndDate.map((date) => {
            const start = moment(date.value?.split(',')[0], 'DD/MM/YYYY');
            const end = moment(date.value?.split(',')[1], 'DD/MM/YYYY');
            if (end && start.isAfter(end)) invalidDates.push(date?.split(',')[4]);
        });
        setInvalidDateRanges(invalidDates);
        return invalidDates.filter((o) => o).length > 0;
    };

    const dateRangeOverlaps = (a_start, a_end, b_start, b_end) => {
        const aStart = a_start && new Date(a_start);
        const aEnd = a_end && new Date(a_end);
        const bStart = b_start && new Date(b_start);
        const bEnd = b_end && new Date(b_end);

        if (aStart && bStart && !aEnd && !bEnd) return (aStart >= bStart) || (bStart >= aStart); // date range overlap but no end dates
        if (aStart && bStart && !aEnd) return (bStart <= aStart && bEnd >= aStart) || (aStart <= bStart && aStart <= bEnd); // date range overlap but no aEnd
        if (aStart && bStart && !bEnd) return (aStart <= bStart && aEnd >= bStart) || (bStart <= aStart && bStart <= aEnd); // date range overlap but no bEnd
        if (aStart && bStart) return (aStart >= bStart && aStart <= bEnd) || (bStart >= aStart && bStart <= aEnd); // date range overlap
    };

    const checkDateRanges = () => {
        var i, j;
        let overlappedDates = [];
        let datesLessThanToday = [];

        for (i = 0; i < parameters.startEndDate.length ; i++) {
          for (j = 0; j < parameters.startEndDate.length; j++) {
            const fieldStart = parameters.startEndDate[i].value?.split(',')[0]
                && parameters.startEndDate[i].value?.split(',')[0] !== 'Invalid date'
                ? moment(parameters.startEndDate[i].value?.split(',')[0], 'DD/MM/YYYY') : '';
            const fieldEnd = parameters.startEndDate[i].value?.split(',')[1]
                && parameters.startEndDate[i].value?.split(',')[1] !== 'Invalid date'
                ? moment(parameters.startEndDate[i].value?.split(',')[1], 'DD/MM/YYYY') : '';
            const otherFieldStart = parameters.startEndDate[j].value?.split(',')[0]
                && parameters.startEndDate[j].value?.split(',')[0] !== 'Invalid date'
                ? moment(parameters.startEndDate[j].value?.split(',')[0], 'DD/MM/YYYY') : '';
            const otherFieldEnd = parameters.startEndDate[j].value?.split(',')[1]
                && parameters.startEndDate[j].value?.split(',')[1] !== 'Invalid date'
                ? moment(parameters.startEndDate[j].value?.split(',')[1], 'DD/MM/YYYY') : '';
            const isFieldStartHistorical = (parameters.startEndDate[i].value?.split(',')[2] && parameters.startEndDate[i].value?.split(',')[2] === 'true');
            const isFieldEndHistorical = (parameters.startEndDate[i].value?.split(',')[3] && parameters.startEndDate[i].value?.split(',')[3] === 'true');
            const isDateOverlapping = dateRangeOverlaps(fieldStart, fieldEnd, otherFieldStart, otherFieldEnd) && i !== j;
            const isDateLessThanToday = fieldStart && moment(fieldStart).isBefore(moment(dateTodayWithoutTimeStamp, 'DD/MM/YYYY'));

            if (isDateOverlapping && (!isFieldStartHistorical || !isFieldEndHistorical)) overlappedDates.push(parameters.startEndDate[i].value?.split(',')[4]);
            if (isDateLessThanToday && !isFieldStartHistorical) datesLessThanToday.push(parameters.startEndDate[i].value?.split(',')[4]);
          }
        }
        setOverlappingDates(overlappedDates);
        setLessThanTodayDates(datesLessThanToday);
        return overlappedDates.length > 0 || datesLessThanToday.length > 0;
    };
    const handleSubmit = () => {
        const hasInvalidDates = checkDateRangeIfValid();
        const hasWrongDates = checkDateRanges();
        setIsSaving(true);
        if (hasParameters()
            && parameters.startEndDate && parameters.startEndDate.every(o => !o.value?.split(',')[0])
        ) {
            setIsSaving(false);
            setStartRangeIsRequiredMessage('Start date is required');
            return;
        } else {
            setStartRangeIsRequiredMessage('');
        }

        if (hasParameters() && (hasWrongDates || hasInvalidDates)) {
            setIsSaving(false);
            return;
        }

        parameters.startEndDate = parameters.startEndDate.filter((o) => o.value?.split(',')[0]);
        parameters.startEndDate = parameters.startEndDate.map((date) => `${date.value.split(',')[0]},${date.value.split(',')[1]}`);

        parameters.duration = parameters.duration ? parameters.duration + ':00' : '';

        if (!hasParameters()) {
            parameters.startEndDate = [''];
        }
        
        AttributesOverrideService.saveAttributesOverride(data.titleId, parameters)
        .then(() => {
            success('Attributes override successfully updated');
            getTitles();
            getTitleAttributes();
            setParameters({});
        })
        .finally(() => setIsSaving(false));
    };

    const convertToLocalTimeWithoutTime = (localDate) => {
        const day = localDate.split('/')[0];
        const monthIndex = localDate.split('/')[1] - 1;
        const year = localDate.split('/')[2];
        const timeWithoutTime = new Date(parseInt(year), parseInt(monthIndex), parseInt(day));
        timeWithoutTime.setHours(0, 0, 0, 0);
        return timeWithoutTime;
    };

    const checkIfHistoricalDate = (date) => {
        const dateWithoutTime = convertToLocalTimeWithoutTime(date);
        const dateTodayWithoutTime = new Date();
        dateTodayWithoutTime.setHours(0, 0, 0, 0);
        const isHistorical = dateWithoutTime < dateTodayWithoutTime;
        return isHistorical;
    };

    const constructLookup = (data) => {
        return data.map((item, i) => ({
            id: i,
            name: item.name,
        }));
    };

    const getCertificateProfileLookup = () => {
        setIsGettingCertificateLookup(true);
        AttributesOverrideService.getCertificateProfileLookup()
        .then((response) => {
            const result = constructLookup(response.data);
            setCertificateProfileLookup(result);
        })
        .finally(() => setIsGettingCertificateLookup(false));
    };

    const getGenreProfileLookup = () => {
        setIsGettingGenreLookup(true);
        AttributesOverrideService.getGenreProfileLookup()
        .then((response) => {
            const result = constructLookup(response.data);
            setGenreProfileLookup(result);
        })
        .finally(() => setIsGettingGenreLookup(false));
    };

    const getGradeProfileLookup = () => {
        setIsGettingGradeLookup(true);
        AttributesOverrideService.getGradeProfileLookup()
        .then((response) => {
            const result = constructLookup(response.data);
            setGradeProfileLookup(result);
        })
        .finally(() => setIsGettingGradeLookup(false));
    };

    const getList = (label) => {
        if (label.toLowerCase() === Certificate.toLowerCase()) return certificateProfileLookup;
        if (label.toLowerCase() === Genre.toLowerCase()) return genreProfileLookup;
        if (label.toLowerCase() === Grade.toLowerCase()) return gradeProfileLookup;
    };

    const onSelectAttribute = (data, name) => {
        setParameters({ ...parameters, [name]: data.name });
    };

    const handleSetDisabledLookups = () => {
        setDisabledLookups({
            certificate: certificateProfileLookup.length === 0,
            genre: genreProfileLookup.length === 0,
            grade: gradeProfileLookup.length === 0,
        });
    };

    const isDateRangeDisabled = () => {
        return !parameters.genre && !parameters.grade && !parameters.certificate && !parameters.title && !parameters.yearOfProduction && !parameters.duration
    };

    useEffect(() => {
        getCertificateProfileLookup();
        getGenreProfileLookup();
        getGradeProfileLookup();
    }, []);

    useEffect(() => {
        handleSetDisabledLookups();
    }, [certificateProfileLookup, genreProfileLookup, gradeProfileLookup]);

    useEffect(() => {
        if (data && !isGettingCertificateLookup && !isGettingGenreLookup && !isGettingGradeLookup) {
            setParameters({
                title: data.overrideValues.title,
                genre: data.overrideValues.genre,
                grade: data.overrideValues.grade,
                certificate: data.overrideValues.certificate,
                yearOfProduction: data.overrideValues.yearOfProduction,
                duration: data.overrideValues.duration,
                rating: data.overrideValues.rating,
                startEndDate: data.overrideValues.startEndDate?.length > 0
                ? data.overrideValues.startEndDate
                    .sort()
                    .filter((o) => o.split(',')[0] || o.split(',')[1])
                    .map((o, i) => {
                        const historicalStartDate = o?.split(',')[0] && checkIfHistoricalDate(o?.split(',')[0]);
                        const historicalEndDate = o?.split(',')[1] && checkIfHistoricalDate(o?.split(',')[1]);
                        const id = i + 1;

                        return {
                            id,
                            value: `${o?.split(',')[0]},${o?.split(',')[1]},${historicalStartDate},${historicalEndDate},${i}`,
                            dayStart: null, dayEnd: null, monthStart: null, monthEnd: null, yearStart: null, yearEnd: null 
                        };
                    })
                : [{ id: 0, value: ',,false,false,0', dayStart: null, dayEnd: null, monthStart: null, monthEnd: null, yearStart: null, yearEnd: null }],
                });
        } else {
            setParameters({});
        }
        setStartRangeIsRequiredMessage('');
        setInvalidDateRanges([]);
        setOverlappingDates([]);
    }, [data, isGettingCertificateLookup, isGettingGenreLookup, isGettingGradeLookup]);

    return (
        <div className={styles.box}>
            {isFetching || isSaving ? (
                <LottieGlassSpinner />
            ) : (
                <>
                    <div className="position-relative">
                        <i>
                            <img
                                className={styles.closeIcon}
                                src={closeIcon}
                                alt=""
                                onClick={onHide}
                                onKeyDown={onHide}
                                role="presentation"
                            />
                        </i>
                    </div>

                    <div className={styles.title} title={data.title}>{data.title}</div>

                    <div className={styles.body}>
                        <div className={styles.details}>
                            <div className={styles.detailColumn}>
                                <div title={data.referenceNo} className={styles.detailName}>
                                    <span>Title ID:</span> {data.referenceNo}
                                </div>
                                {data.library == true &&
                                <>
                                    <div title={data.dealCode} className={styles.detailName}>
                                        <span>Deal Code:</span> {data.dealCode}
                                    </div>
                                    <div title={data.dealName} className={styles.detailName}>
                                        <span>Deal Name:</span> {data.dealName}
                                    </div>
                                </>
                                }
                                {data.library == false &&
                                <>
                                    <div title={data.studio} className={styles.detailName}>
                                        <span>Studio:</span> {data.studio}
                                    </div>
                                    <div title={data.playRestriction} className={styles.detailName}>
                                        <span>Play Restriction:</span> {data.playRestriction}
                                    </div>
                                </>
                                }

                            </div>
                            {data.library == true &&
                                <>
                            <div className={styles.detailColumn}>
                                <div title={data.dayType} className={styles.detailName}>
                                    <span>Day Type:</span> {data.dayType}
                                </div>
                                <div title={data.dayPeriod} className={styles.detailName}>
                                    <span>Day Period:</span> {data.dayPeriod}
                                </div>
                                <div title={data.exhibPlays} className={styles.detailName}>
                                    <span>Exhib Plays:</span> {data.exhibPlays}
                                </div>
                            </div>
                                </>
                                }

                        </div>

                        <div className={styles.form}>
                            {attributesOverrideFields.map((item) => (
                                <>
                                    {checkTitleSource(item.titleSource) && (
                                        <div className={styles.formInput}>
                                            { item.type !== 'multiple_input' ? (
                                                <>
                                                    <label className={styles.formInputLabel} title={data[item.name]}>
                                                        <span>{item.label}:</span> {data[item.name]}
                                                    </label>
                                                    
                                                    <div ref={item.label === inputRefName ? inputRef : null}>
                                                        {item.type === 'time' ? (
                                                            <TimeInput
                                                                noIcon
                                                                value={parameters[item.name]}
                                                                name={item.name}
                                                                placeholder={item.placeholder}
                                                                onChange={onChangeTimeInputSetParameters}
                                                            />
                                                        ) : (
                                                            <>
                                                                {item.type === 'select' ? (
                                                                    <SelectComponent
                                                                        list={() => getList(item.label)}
                                                                        text={parameters[item.name] || 'Please select an override value'}
                                                                        onSelect={(data) => onSelectAttribute(data, item.name)}
                                                                        disabled={disabledLookups[item.name]}
                                                                        withSearch
                                                                        width="100%"
                                                                    />
                                                                ) : (
                                                                    <Input
                                                                        value={parameters[item.name]}
                                                                        name={item.name}
                                                                        placeHolder={item.placeholder}
                                                                        maxLength={item.maxLength}
                                                                        onChange={(e) => onChangeSetParameters(e, item.label, item.type)}
                                                                    />
                                                                )}
                                                            </>
                                                        )}
                                                    </div>
                                                </>
                                            ) : parameters.startEndDate?.length > 0 && (
                                                <>
                                                    {parameters.startEndDate.map((x, index) => (
                                                        <div className="w-100" key={index}>
                                                            {index === 0  && (
                                                                <div className="d-flex">
                                                                    <div className={styles.dateRangePickerLabel}>Start:</div>
                                                                    <div className={styles.dateRangePickerLabel}>End:</div>
                                                                </div>
                                                            )}
                                                            <div className="d-flex w-100">
                                                                <div className="w-100 mb-1">
                                                                    <CustomDateRangeInput
                                                                        key={x.id}
                                                                        min={new Date()}
                                                                        onChange={(e) => onChangeSetParameters({
                                                                            target: { value: e }}, `${'start-end'}-${index}`, 'start_end_date', x.value?.split(',')[2], x.value?.split(',')[3], x.value?.split(',')[4]
                                                                        )}
                                                                        startSettings={{
                                                                            value: !(x.value?.split(",")[0]) || x.value?.split(",")[0] === ""? null : moment(x.value?.split(",")[0], "DD/MM/YYYY").toDate(),
                                                                            label: null,
                                                                            disabled: x.value?.split(',')[2] === 'true' || isDateRangeDisabled(),
                                                                            dayStart: x.dayStart,
                                                                            monthStart: x.monthStart,
                                                                            yearStart: x.yearStart,
                                                                        }}
                                                                        endSettings={{
                                                                            value: !(x.value?.split(",")[1]) || x.value?.split(",")[1] === ""? null :moment(x.value?.split(",")[1], "DD/MM/YYYY").toDate(),
                                                                            label: null,
                                                                            disabled: x.value?.split(',')[3] === 'true' || isDateRangeDisabled(),
                                                                            dayEnd: x.dayEnd,
                                                                            monthEnd: x.monthEnd,
                                                                            yearEnd: x.yearEnd,
                                                                        }}
                                                                    />
                                                                </div>
                                                                <div>
                                                                    {index === 0 ?  (
                                                                        <i className={styles.plusBoxIcon} title="Add Date">
                                                                            <img
                                                                                className={styles.plusCircleIcon}
                                                                                src={plusCircleIcon}
                                                                                alt=""
                                                                                role="presentation"
                                                                                onClick={() => {
                                                                                    if(!isDateRangeDisabled()) {
                                                                                        setParameters({
                                                                                            ...parameters,
                                                                                            startEndDate: [ ...parameters.startEndDate,{id: parameters.startEndDate.length > 0
                                                                                                ? (maxId === 0 ? parameters.startEndDate.length : 0) + maxId + 1 : 1 ,value: `,,false,false,${parameters.startEndDate.length > 0
                                                                                                ? (maxId === 0 ? parameters.startEndDate.length : 0) + maxId + 1 : 1}`, dayStart: null, dayEnd: null, monthStart: null, monthEnd: null, yearStart: null, yearEnd: null }],
                                                                                        })
                                                                                        if (maxId === 0) setMaxId(parameters.startEndDate.length + 1)
                                                                                        else setMaxId(maxId + 1);
                                                                                    }
                                                                                }}
                                                                            />
                                                                        </i>
                                                                    ) : (
                                                                        <i className={styles.trashCanSmIcon} title="Remove Dates">
                                                                            <img
                                                                                className={styles.trashcanIcon}
                                                                                src={trashCanSmIcon}
                                                                                alt=""
                                                                                role="presentation"
                                                                                onClick={() => {
                                                                                    if(!isDateRangeDisabled()){
                                                                                        parameters.startEndDate.splice(index, 1); 
                                                                                        setParameters({ ...parameters, startEndDate: parameters.startEndDate })
                                                                                    }
                                                                                }}
                                                                            />
                                                                        </i>
                                                                    )}
                                                                </div>
                                                            </div>
                                                            {startRangeIsRequiredMessage && index === 0 && (
                                                                <div className={styles.errorText}>{startRangeIsRequiredMessage}</div>
                                                            )}
                                                            {overlappingDates.includes(x.value?.split(",")[4]) && (
                                                                <div className={styles.errorText}>Overlapping date range is not allowed</div>
                                                            )}
                                                            {invalidDateRanges.includes(x.value?.split(",")[4]) && (
                                                                <div className={styles.errorText}>Date range is invalid</div>
                                                            )}
                                                            {lessThanTodayDates.includes(x.value?.split(",")[4]) && (
                                                                <div className={styles.errorText}>Start should not be earlier than the current date</div>
                                                            )}
                                                        </div>
                                                    ))}
                                                </>
                                            )}
                                        </div>
                                    )}
                                </>
                            ))}
                        </div>

                        <div className={styles.details}>
                            <div className={styles.detailColumn}>
                                <div className={styles.detailName}>
                                    <span>Channel</span>
                                </div>
                            </div>
                            <div style={{ width: '80x', minWidth: '80px', overflowX: 'hidden', marginRight: '10px' }}>
                                <div className={styles.detailName}>
                                    <span>Window Start</span>
                                </div>
                            </div>
                            <div style={{ width: '80x', minWidth: '80px', overflowX: 'hidden' }}>
                                <div className={styles.detailName}>
                                    <span>Window End</span>
                                </div>
                            </div>
                        </div>

                        {data.channels && data.channels.map((channel, index) => (
                            <div
                                key={`${index}:${channel.name}`}
                                className={styles.details}
                            >
                                <div className={styles.detailColumn}>
                                    <div className={styles.detailName} title={channel.name}>
                                        {channel.name}
                                    </div>
                                </div>
                                <div style={{ width: '80x', minWidth: '80px', overflowX: 'hidden', marginRight: '10px' }}>
                                    <div className={styles.detailName} title={channel.windowsStartDate}>
                                        {channel.windowsStartDate}
                                    </div>
                                </div>
                                <div style={{ width: '80x', minWidth: '80px', overflowX: 'hidden' }}>
                                    <div className={styles.detailName} title={channel.windowsEndDate}>
                                        {channel.windowsEndDate}
                                    </div>
                                </div>
                            </div>
                        ))}
                    </div>

                    <div className={styles.footer}>
                        <i className={styles.saveIcon} title="Update title">
                            <img
                                src={saveIcon}
                                style={{ pointerEvents: isSaving && 'none' }}
                                alt=""
                                onClick={handleSubmit}
                                onKeyDown={handleSubmit}
                                role="presentation"
                            />
                        </i>
                        <i className={styles.closeCircleIcon} title="Cancel">
                            <img
                                src={closeCircleIcon}
                                alt=""
                                onClick={onHide}
                                onKeyDown={onHide}
                                role="presentation"
                            />
                        </i>
                    </div>
                </>
            )}
        </div>
    )
};

export default AttributesOverrideFormComponent;