import { useEffect, useState, useRef } from 'react';

import styles from './styles.module.css'
import { clockIcon, chevDownGreyIcon, chevUpGreyIcon } from '../../assets';

const TimeInput = ({
    value,
    name,
    placeholder,
    onChange,
    format = 'HH:MM:SS',
    width,
    noIcon,
}) => {
    const [renderTimes, setRenderTimes] = useState(0);
    const timeDropdownRef = useRef();
    const hourTextFieldRef = useRef();
    const minuteTextFieldRef = useRef();
    const secondTextFieldRef = useRef();
    const iconRef = useRef();
    const [showTimeDropdown, setShowTimeDropdown] = useState(false);
    const [inputValue, setInputValue] = useState('');
    const [changeTimes, setChangeTimes] = useState(0);
    const [showMainTextField, setShowMainTextField] = useState(false);

    const getNumbersOnly = (string) => {
        const stringArray = string.split('');
        const result = stringArray.filter((char) => (
            !isNaN(char) && !isNaN(parseFloat(char)) || char === ':'
        ));
        return result.join('');
    }

    const getValue = (time, timeTypeId) => {
        const timeValue = time?.split(':')[timeTypeId];
        if (timeValue && timeValue.length === 3) {
            if (timeValue.substring(0,2) == "00") return timeValue.substring(1,3);
            else if (timeValue.substring(0,1) == "0") return timeValue.substring(1,3);
        }
        return timeValue || '';
    };

    const hourValue = (inputValue && getValue(inputValue, 0)) || '00';
    const minuteValue = (inputValue && getValue(inputValue, 1)) || '00';
    const secondValue = (inputValue &&  getValue(inputValue, 2)) || '00';
    
    const changeHour = (action) => {  
        if (action === 'increase') {
            if (hourValue > 23) return false;
            const time = (parseInt(hourValue) + 1)
                + (format.includes('MM') ? ':' + minuteValue : '')
                + (format.includes('MM') && format.includes('SS') ? ':' + secondValue : '');
            setInputValue(time);
        }
        else {
            if (hourValue < 1) return false;
            const time = (parseInt(hourValue) - 1)
                + (format.includes('MM') ? ':' + minuteValue : '')
                + (format.includes('MM') && format.includes('SS') ? ':' + secondValue : '');
            setInputValue(time);
        }
        setChangeTimes(changeTimes + 1);
    };

    const changeMinute = (action) => {
        if (action === 'increase') {
            if (minuteValue > 58) return false;
            const time = hourValue 
                + ':' + (parseInt(minuteValue) + 1)
                + (format.includes('MM') && format.includes('SS') ? ':' + secondValue : '');
            setInputValue(time);
        }
        else {
            if (minuteValue < 1) return false;
            const time = hourValue
                + ':' + (parseInt(minuteValue) - 1)
                + (format.includes('MM') && format.includes('SS') ? ':' + secondValue : '');
            setInputValue(time);
        }
        setChangeTimes(changeTimes + 1);
    };

    const changeSecond = (action) => {
        if (action === 'increase') {
            if (secondValue > 58) return false;
            const time = hourValue + ':' + minuteValue + ':' + (parseInt(secondValue) + 1);
            setInputValue(time);
        }
        else {
            if (secondValue < 1) return false;
            const time = hourValue + ':' + minuteValue + ':' + (parseInt(secondValue) - 1);
            setInputValue(time);
        }
        setChangeTimes(changeTimes + 1);
    };

    const onChangeTimeFromDropdown = (timeValue, timeType) => {
        let newTimeValue = timeValue;

        if (newTimeValue.length === 2) {
            if (timeType === 0) {
                if (newTimeValue > 24) newTimeValue = `0${newTimeValue.substring(1,2)}`
                else newTimeValue = newTimeValue.substring(0,2)
            } else {
                if (newTimeValue > 60) newTimeValue = `0${newTimeValue.substring(1,2)}`
                else newTimeValue = newTimeValue.substring(0,2)
            }
        }

        if (newTimeValue.length === 3) {
            newTimeValue = newTimeValue.substring(1,3);
            if (newTimeValue > 24 && timeType === 0) newTimeValue = `0${newTimeValue.substring(1,2)}`
            else if (newTimeValue > 60) newTimeValue = `0${newTimeValue.substring(1,2)}`
        }

        if (newTimeValue.length === 0) newTimeValue = '00';

        if (newTimeValue.length === 1) newTimeValue = `0${newTimeValue}`;

        switch(timeType) {
            case 0:
                if (newTimeValue[0] > 3
                    && newTimeValue.length > 1
                    && newTimeValue > 25
                ) newTimeValue = `0${newTimeValue.substring(1,2)}`;
                setInputValue(newTimeValue + ':' + (minuteValue || '00') + ':' + (secondValue || '00'));
                break;
            case 1:
                if (newTimeValue[1] > 5
                    && newTimeValue.length > 1
                    && newTimeValue > 60
                ) newTimeValue = `0${newTimeValue.substring(1,2)}`;
                setInputValue((hourValue || '00') + ':' + newTimeValue + ':' + (secondValue || '00'));
                break;
            case 2:
                if (newTimeValue[2] > 5
                    && newTimeValue.length > 1
                    && newTimeValue > 60
                ) newTimeValue = `0${newTimeValue.substring(1,2)}`;
                setInputValue((hourValue || '00') + ':' + (minuteValue || '00') + ':' + newTimeValue);
                break;
            default:
                break;
        };

        setChangeTimes(changeTimes + 1);
    };

    const openTimeDropdown = () => setShowTimeDropdown(true);

    const backdropClickedCloseDropdown = (e) => {
        if (timeDropdownRef.current && !timeDropdownRef.current.contains(e.target)) {
            setShowTimeDropdown(false);
        }
    };

    const addZeroAtTheEndOfMinute = (minute) => {
        const isTwoDigit = minute.length === 2;
        const isOneDigit = minute.length === 1;
        const isFirstDigitEqualsToSix = minute[0] === '6';
        const isFirstDigitGreaterThanSix = parseInt(minute[0]) > 6;

        if (isFirstDigitEqualsToSix) return '06';
        if (isFirstDigitGreaterThanSix) return '0' + minute[0];

        return isTwoDigit ? minute
            : isOneDigit && '0' + minute
            || '00';
    };

    const onBlurTimeInput = (e) => {
        const value = hourValue
        + (format.includes('MM')
        ? ':'
        + addZeroAtTheEndOfMinute(minuteValue)
        : '')
        + (format.includes('MM') && format.includes('SS')
        ? ':'
        + addZeroAtTheEndOfMinute(secondValue)
        : '');
        setInputValue(value);

        if (value === '00:00:00') {
            if ((hourTextFieldRef.current?.contains(e.target)
                || minuteTextFieldRef.current?.contains(e.target)
                || secondTextFieldRef.current?.contains(e.target)
                || iconRef.current?.contains(e.target)
                || timeDropdownRef.current?.contains(e.target))) return;
            setShowMainTextField(false);
        }
    };

    const setFocusToHourTextField = (e) => {
        if (minuteTextFieldRef.current?.contains(e.target)
            || secondTextFieldRef.current?.contains(e.target)
            || iconRef.current?.contains(e.target)
            || timeDropdownRef.current?.contains(e.target)
        ) {
            return;
        }
        setShowMainTextField(true);
        hourTextFieldRef.current?.focus();
    };

    const onChangeFocus = (e, timeDigit) => {
        const hour = 0;
        const minute = 1;
        const second = 2;
        const key = e.key;

        if (timeDigit === hour
            && format.includes('MM')
            && key === 'ArrowRight'
        ) { 
            minuteTextFieldRef.current?.focus();
        }

        if (timeDigit === minute
            && key === 'ArrowLeft'
        ) {
            hourTextFieldRef.current?.focus();
        }

        if (timeDigit === minute
            && format.includes('SS')
            && key === 'ArrowRight'
        ) {
            secondTextFieldRef.current?.focus();
        }

        if (timeDigit === second
            && key === 'ArrowLeft'
            && format.includes('MM')
        ) {
            minuteTextFieldRef.current?.focus();
        }
    };

    const onFocusPlaceholderInput = () => {
        setShowMainTextField(true);
    };

    const onFocusTextField = (e) => {
        e.target.setSelectionRange(e.target.value.length, e.target.value.length)
    }

    useEffect(() => {
        if (renderTimes === 0) setInputValue(value);
        setRenderTimes(renderTimes + 1);
    }, [value]);

    useEffect(() => {
        if (renderTimes > 0) onChange(inputValue === '00:00:00' ? '' : inputValue, name);
    }, [inputValue, renderTimes]);

    useEffect(() => {
        if (showMainTextField) hourTextFieldRef.current?.focus();
    }, [showMainTextField]);

    useEffect(() => {
        document.addEventListener('mouseup', onBlurTimeInput);
        return () => document.removeEventListener('mouseup', onBlurTimeInput);
    });

    useEffect(() => {
        document.addEventListener('mousedown', backdropClickedCloseDropdown);
        return () => document.removeEventListener('mousedown', backdropClickedCloseDropdown);
    });

    return (
        <div
            className={styles.timeInput}
            style={{ width: width }}
            onClick={setFocusToHourTextField}
        >
            {showMainTextField
                || (inputValue && inputValue !== '00:00:00') ? (
                <>
                    <div className={styles.digitInput}>
                    <input
                        ref={hourTextFieldRef}
                        className={`${styles.input}
                            ${getValue(inputValue, 0).includes('1')
                            ? styles.smallerWidth : styles.largerWidth}
                        `}
                        value={getValue(inputValue, 0)}
                        placeholder={placeholder}
                        onBlur={onBlurTimeInput}
                        onFocus={onFocusTextField}
                        onChange={(e) => (
                            onChangeTimeFromDropdown(
                                getNumbersOnly(e.target.value),
                                0,
                            )
                        )}
                        onKeyDown={(e) => onChangeFocus(e, 0)}
                    />
                </div>
                <div className={styles.smallSemiColon}>:</div>
                <div className={styles.digitInput}>
                    <input
                        ref={minuteTextFieldRef}
                        className={`${styles.input}
                            ${getValue(inputValue, 1).includes('1')
                            ? styles.smallerWidth : styles.largerWidth}
                        `}
                        value={format.includes('MM') ? getValue(inputValue, 1) : '00'}
                        placeholder={placeholder}
                        onBlur={onBlurTimeInput}
                        onFocus={onFocusTextField}
                        onChange={(e) => (
                            format.includes('MM')
                            && onChangeTimeFromDropdown(
                                getNumbersOnly(e.target.value),
                                1,
                            )
                        )}
                        onKeyDown={(e) => onChangeFocus(e, 1)}
                    />
                </div>
                {format.includes('SS') && (
                    <>
                        <div className={styles.smallSemiColon}>:</div>
                        <div className={styles.digitInput}>
                            <input
                                ref={secondTextFieldRef}
                                className={`${styles.input}
                                    ${getValue(inputValue, 2).includes('1')
                                    ? styles.smallerWidth : styles.largerWidth}
                                `}
                                value={getValue(inputValue, 2)}
                                placeholder={placeholder}
                                onBlur={onBlurTimeInput}
                                onFocus={onFocusTextField}
                                onChange={(e) => (
                                    onChangeTimeFromDropdown(
                                        getNumbersOnly(e.target.value),
                                        2,
                                    )
                                )}
                                onKeyDown={(e) => onChangeFocus(e, 2)}
                            />
                        </div>
                    </>
                )}
                </>
            ) : (
                <input
                    className={styles.placeHolderInput}
                    placeholder={placeholder}
                    onFocus={() => onFocusPlaceholderInput}
                />
            )}

            <div ref={iconRef} className={styles.icon} onClick={openTimeDropdown} hidden={noIcon}>
                <img className={styles.clockIcon} src={clockIcon} />

                {showTimeDropdown && (
                    <div ref={timeDropdownRef} className={styles.timeDropdown}>
                        <div className={styles.hour}>
                            <img
                                className={styles.chevGreyIcon}
                                src={chevUpGreyIcon}
                                alt=""
                                onClick={() => changeHour('increase')}
                            />
                            <input
                                className={styles.hourInput}
                                value={getValue(inputValue, 0)}
                                placeholder="00"
                                onFocus={onFocusTextField}
                                onChange={(e) => (
                                    onChangeTimeFromDropdown(
                                        getNumbersOnly(e.target.value),
                                        0,
                                    )
                                )}
                            />
                            <img
                                className={styles.chevGreyIcon}
                                src={chevDownGreyIcon}
                                alt=""
                                onClick={() => changeHour('decrease')}
                            />
                        </div>

                        {format.includes('MM') && (
                            <>
                                <div className={styles.semiColon}>:</div>

                                <div className={styles.minute}>
                                    <img
                                        className={styles.chevGreyIcon}
                                        src={chevUpGreyIcon}
                                        alt=""
                                        onClick={() => changeMinute('increase')}
                                    />
                                    <input
                                        className={styles.minuteInput}
                                        value={getValue(inputValue, 1)}
                                        placeholder="00"
                                        onFocus={onFocusTextField}
                                        onChange={(e) => (
                                            onChangeTimeFromDropdown(
                                                getNumbersOnly(e.target.value),
                                                1,
                                            )
                                        )}
                                    />
                                    <img
                                        className={styles.chevGreyIcon}
                                        src={chevDownGreyIcon}
                                        alt=""
                                        onClick={() => changeMinute('decrease')}
                                    />
                                </div>
                            </>
                        )}

                        {format.includes('MM') && format.includes('SS') && (
                            <>
                                <div className={styles.semiColon}>:</div>

                                <div className={styles.second}>
                                    <img
                                        className={styles.chevGreyIcon}
                                        src={chevUpGreyIcon}
                                        alt=""
                                        onClick={() => changeSecond('increase')}
                                    />
                                    <input
                                        className={styles.secondInput}
                                        value={getValue(inputValue, 2)}
                                        placeholder="00"
                                        onFocus={onFocusTextField}
                                        onChange={(e) => (
                                            onChangeTimeFromDropdown(
                                                getNumbersOnly(e.target.value),
                                                2,
                                            )
                                        )}
                                    />
                                    <img
                                        className={styles.chevGreyIcon}
                                        src={chevDownGreyIcon}
                                        alt=""
                                        onClick={() => changeSecond('decrease')}
                                    />
                                </div>
                            </>
                        )}
                    </div>
                )}
            </div>
        </div>
    )
}

export default TimeInput;